import {Component,OnInit,Renderer,ElementRef} from '@angular/core';
import { Routes,Route,Router,ROUTER_DIRECTIVES,ROUTER_PROVIDERS } from '@angular/router';

//import {MdButton, MdAnchor} from '../components/button/button';
import {MdButton, MdAnchor} from '@angular2-material/button';
//import {MdIcon} from '../components/icon/icon';
//import {MD_LIST_DIRECTIVES} from '../components/list/list';
import {PageScroll} from './page-scroll-directive/page-scroll.directive';

import { HeroPortalComponent } from './hero-portal/hero-portal.component';
import { TodoPortalComponent } from './todo-portal/todo-portal.component';
import { DownupAnimationComponent } from './xxj-animation/downup-animation.component';
import { DemoApp } from '../demo-app/demo-app';
import { CoupleCard } from './couple-card/couple-card.component';
import { UseBroadcastParentComponent } from './use-custom-broadcast/use-broadcast-parent.component';
import { NeedLoadComponentComponent } from './load-component-in/need-load-component.component';

import { TopFrameComponent } from './top-frame/top-frame.component';

export class Element {
    constructor(private nodeName: string, private parent?: Element) {
    }
    toString() {
        return '<' + this.nodeName + '>';
    }
};

@Component({
    selector:'my-app',
    templateUrl:"app/app.component.html",
    styleUrls:["app/app.component.css"],
    host:{
          'margin-top':'1em',
          'position':"absolute",
          "bottom":"1%"
        },
    directives:[
                ROUTER_DIRECTIVES,
                MdButton, MdAnchor,
                PageScroll,TopFrameComponent
               ],
    providers:[]
})

@Routes([
new Route({ path:'/hero-portal', component:HeroPortalComponent}),
new Route({ path:'/todo-portal', component:TodoPortalComponent}),
new Route({ path:'/xxj-animation', component:DownupAnimationComponent}),
new Route({ path:'/demo-app', component:DemoApp}),
new Route({ path:'/couple-card', component:CoupleCard}),
new Route({ path:'/use-custom-broadcast', component:UseBroadcastParentComponent}),
new Route({ path:'/load-component-in', component:NeedLoadComponentComponent})
])

export class AppComponent implements OnInit {
    title='This is Home';
    htmlString:string="<h1>test</h1>";
    constructor(private _r:Router,
                private _renderer:Renderer,private _el:ElementRef){}
    ngOnInit(){
        this._r.navigate(['/hero-portal']);
        
    }
   
}

